define('adminclient/func/fuzzymatch', ['exports'], function (exports) {

  'use strict';

  exports['default'] = function (a, b) {
    var a = a.toLowerCase();
    var b = b.toLowerCase();
    var n = -1;
    for (var i = 0, len = b.length; i < len; i++) {
      var l = b[i];
      if (! ~(n = a.indexOf(l, n + 1))) {
        return false;
      }
    }
    return true;
  };

});