define('adminclient/func/objectFilter', ['exports'], function (exports) {

  'use strict';


  exports['default'] = function (obj, filter) {
    return Object.keys(obj).reduce(function (out, key) {

      var val = obj[key];

      if (filter(val, key)) {
        out[key] = val;
      }

      return out;
    }, {});
  };

});